@media (width <= 990px) {
  html {
    font-size: 55.5%;
  }

  .home {
    flex-flow: column-reverse;
    padding: 15rem 0 8rem;
  }
}

@media (width <= 700px) {
  html {
    font-size: 50.5%;
  }

  .home img {
    width: 35rem;
    height: 35rem;
  }

  .home {
    padding: 8rem 0;
  }

  section {
    padding: 1rem;
  }

  .header {
    padding: 0;
    position: relative;
  }

  .header .logo, .header .menu {
    padding: 1rem;
  }

  .menu i {
    display: flex;
  }

  .header .navbar.active {
    display: grid;
  }

  .header .navbar {
    text-align: center;
    background-color: var(--main-color);
    justify-content: center;
    gap: 2rem;
    width: 100%;
    padding: 1rem;
    display: none;
    position: absolute;
    top: 7rem;
  }

  .header .navbar a#Home {
    color: var(--text-color);
    text-decoration: none;
  }

  .header .navbar a {
    width: 100%;
    padding: 1rem;
  }

  .header .navbar a:hover, .header .navbar a#Home:hover {
    background-color: var(--white);
    color: var(--main-color);
    text-decoration: none;
  }
}

@media (width <= 450px) {
  .education {
    min-height: 30vh;
  }

  .home .content p {
    width: 35rem;
  }

  .education .timeline-items, .contact .form-container form {
    display: grid;
  }

  .contact .form-container form .inputs, .contact .form-container form .text, .contact .form-container form .inputs .input, .contact .form-container form .text textarea {
    width: 100%;
  }

  .contact {
    padding: 1rem 0;
  }
}

@media (width <= 768px) {
  .gallery-section {
    flex-direction: column;
  }

  .gallery-header h2 {
    margin-bottom: 20px;
    position: static;
  }
}

@media (width <= 598px) {
  html {
    font-size: 51.05%;
  }

  .education {
    min-height: 50vh;
  }

  .education .timeline-content {
    width: auto;
  }
}
